@import url("https://fonts.googleapis.com/css?family=Roboto+Slab:100,300,400,700");
@import url("https://fonts.googleapis.com/css?family=Raleway:300,300i,400,400i,500,500i,600,600i,700,700i,800,800i,900,900i");

* {
  margin: 0;
  border: 0;
  box-sizing: border-box;
}

:root {
  --font-roboto: "Roboto Slab", serif;
  --font-raleway: "Raleway", sans-serif;
}

body {
  font-family: var(--font-roboto);
  background-color: #212426;
}

.app {
  padding: 4rem;

  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}
.header{
    display: flex;
    justify-content: space-around;
    flex-wrap: wrap;
    width: 100%;
}
h1 {
  font-size: 3rem;
  letter-spacing: 0.9px;
  font-size: 50px;
  background: linear-gradient(
    90deg,
    rgba(249, 211, 180, 1) 0%,
    rgba(249, 211, 180, 0) 100%
  );
  background-clip: text;
  -webkit-background-clip: text;
  color: transparent;
}
h1 span{
    color: rgb(228, 220, 148);
}
.search {
  width: 71%;
  margin: 4rem 0 2rem;

  display: flex;
  align-items: center;
  justify-content: center;

  padding: 1.5rem 1.75rem;
  border-radius: 3rem;
  background: #1f2123;
  -webkit-box-shadow: 5px 5px 7px #1c1d1f, -5px -5px 7px #222527;
  box-shadow: 5px 5px 7px #1c1d1f, -5px -5px 7px #222527;
}
.menu{
    border: 1px solid rgb(228, 220, 148);
    width: 50px;
    height: 50px;
    border-radius: 5px;
    margin: 10px;
}
.menu svg{
    font-size: 20px;
    color: #eedb8e;
}
.search input {
  flex: 1;
  border: none;
  font-size: 1.5rem;
  font-family: var(--font-raleway);
  font-weight: 500;
  padding-right: 1rem;

  outline: none;
  color: #a1a1a1;
  background: #1f2123;
}

.search img {
  width: 35px;
  height: 35px;
  cursor: pointer;
}
.sidenav {
	height: 100%;
	width: 0; 
	position: fixed; 
	z-index: 1;
	top: 0; 
	left: 0;
	background-color: #111; 
	overflow-x: hidden; 
	padding-top: 70px; 
	transition: 0.5s; 
  }
  
 
  .sidenav a {
	padding: 12px 13px 12px 32px;
	text-decoration: none;
	font-size: 25px;
	color: #d3d0d0;
	display: block;
	transition: 0.3s;
	margin-top: 4%;
  }
  
  
  .sidenav a:hover {
	color: rgb(241, 135, 6);
  }
  

  .sidenav .closebtn {
	position: absolute;
	top: 0;
	right: 25px;
	font-size: 36px;
	margin-left: 50px;
  }
  .sidenav .sidede{
	background-color: rgb(252, 140, 3);
	height: 7vh;
	top: 0;
  }
  .sidenav p{
    color: rgb(224, 202, 0);
    padding: auto;
    font-weight: 500;
    margin: 20px;
    cursor: pointer;
  }
  .sidenav .downde{
	margin-top: 70%;
	text-align: center;
  }
  .privacypolicy{
    color: white;
  }
.privacypolicy p {
    color: yellow;
    cursor: pointer;
}
